import Swiper from "../../node_modules/swiper/swiper-bundle.min.mjs";


  export const initSwiper = ()=> {

 //
 const slideLabels = ["#organic", "#products", "#basket", "#vegetables"]; //      
    new Swiper(".mySwiper", {
   spaceBetween:30,
   setWrapperSize: true,
   autoHeight: true,
   /* autoplay: true, */
   pagination: {
     el: ".hero-slider-pagination",
     clickable: true,
     renderBullet: function (index, className) {
     return (
       '<div class="' + className + ' hero-slider-bullet">' + " " + slideLabels[index] + "</div>"
     );
     },
   },
     breakpoints: {
      768: { //  
        spaceBetween: 20, //  
        slidesPerView: 1, //    
      },
      1024: { //   
        spaceBetween: 30,
        slidesPerView: 1, //    
      },
      },
      });

};

